   description: '<p>Startups brauchen Kapital und Coaching. Aber vor allem brauchen sie schon möglichst früh zahlende Kunden. Das funktioniert mit dem Venture Client Modell. Als Venture Client kauft ein Corporate, anstelle von Eigenkapital, das Produkt eines Startups – auch wenn es sich noch in Prototypen-Phase befindet. Mit der Entwicklung der BMW Startup Garage haben die Gründer von 27pilots Corporate Venturing neu erfunden. Das Venture Client Modell nutzt gezielt die strategischen Stärken beider Partner und wurde inzwischen erfolgreich auch in weiteren Konzernen etabliert.</p> <p>Was bedeutet Venture Client für Startups und Corporates und wie unterscheidet sich das Format von Venture Capital?</p> <p>27pilots unterstützt Firmen bei der Einführung von Venture Client Einheiten und der erfolgreichen Skalierung von Startup Projekten. Dr. Maximilian Marquart, Co-Gründer von 27pilots, stellt uns das Venture Client Modell vor und erzählt von Erfolgsbeispielen.</p> ',
