   link: 'https://www.meetup.com/Happy-Writing-Munich/events/268422505/',
   description: '<p>If you\'re in the modern publishing landscape, you might see social media as a barrier. Some agents require a specific following number to even look at your work. While it\'s not for everyone, and certainly not everyone needs to be strong on social media, if this is an area you want to know more about, we\'re here to teach you!</p> <p>This workshop will be led by Dionne. In addition to teaching Social Media Marketing at EU Business School, Dionne is a certified Goal Achievement Life Coach. She actively coaches authors in building their platform and fine tuning their socials, and has helped build some of the largest Twitter tags and very successful author YouTube accounts with her clients.</p> <p>Location will be announced closer to the workshop depending on how many people sign up.</p> ',
