   link: 'https://www.meetup.com/Munich-Salsa-Bachata-Kizomba-Friends-Meetup/events/268948509/',
   description: '<p>Salsa Party am Stachus with Salsamás &amp; Friends<br/>Salsa Classics, Salsa Cubana, Salsa Romantica, Bachata &amp; Kizomba</p> <p>Every first Saturday !<br/>=&gt; 20:00-03:00h</p> <p>* Free entry for all!<br/>* 20:00-21:00 - dance classes for free</p> <p>The „Tanzschule am Stachus“ invites all salsa friends to the monthly „Salsa Party am Stachus with Salsamás &amp; Friends“. At the beginning of the evening the dance school Salsamás Munich will offer dance classes for free. DJ Nicemixx &amp; Friends will take care of the good music mix! This event is organized with the friendly support of the dance school Salsamás Munich.</p> <p>Of course, the „Tanzschule am Stachus“ team will offer refreshing drinks and delicious cocktails at the bar.</p> <p>We look forward to your coming :-)</p> <p>====================================</p> <p>Timetable:<br/>20: 00-20: 30 - Salsa intermediate class „lifts and dips“ with Salsamás-Team<br/>20: 30-21: 00 - Salsa,Bachata &amp; Kizomba beginner class with Salsamás-Team<br/>22: 00-03: 00 - Best of Salsa Classics, Salsa Cubana, Salsa Romantica, Bachata &amp; Kizomba</p> <p>Tanzschule am Stachus<br/>Sonnenstraße 7 . 80331 München</p> <p>Your „Tanzschule am Stachus“ &amp; Salsamás teams</p> <p>Further information:<br/>Mobile: [masked]<br/>E-Mail: [masked]</p> <p>Deutsch:</p> <p>Salsa Party am Stachus with Salsamás &amp; Friends<br/>Salsa Classics, Salsa Cubana, Salsa Romantica, Bachata &amp; Kizomba</p> <p>====================================<br/>Immer am ersten Samstag des Monats!<br/>====================================</p> <p>Die „Tanzschule am Stachus“ ladet alle Salsa Freunde, zu der monatlichen „Salsa Party am Stachus mit Salsamás &amp; Friends“ ein. Zu Beginn des Abends wird die Tanzschule Salsamás Munich kostenlose Tanzkurse anbieten. DJ Nicemixx &amp; Friends werden für den guten Musikmix sorgen! Dieses Event wird mit der freundlichen Unterstützung der Tanzschule Salsamás Munich organisiert.</p> <p>Selbstverständlich wird die „Tanzschule am Stachus“ alle Gäste mit erfrischenden Getränken und leckeren Cocktails an der Bar verwöhnen.</p> <p>Wir freuen uns auf euer Kommen :-)</p> <p>Salsa Party am Stachus with Salsamás &amp; Friends Salsa Classics, Salsa Cubana, Salsa Romantica, Bachata &amp; Kizomba</p> <p>====================================<br/>Immer am ersten Samstag des Monats!<br/>====================================</p> <p>Programm:<br/>20: 00-20: 30 - Salsa intermediate class „lifts and dips“ with Salsamás-Team<br/>20: 30-21: 00 - Salsa,Bachata &amp; Kizomba beginner class with Salsamás-Team<br/>22: 00-03: 00 - Best of Salsa Classics, Salsa Cubana, Salsa Romantica, Bachata &amp; Kizomba</p> <p>Tanzschule am Stachus<br/>Sonnenstraße 7 . 80331 München</p> <p>Euer „TANZSCHULE AM STACHUS“ &amp; Salsamás Teams</p> <p>Weitere Infos unter: [masked]</p> ',
