   id: '268679736',
   name: 'How To Design Awesome CSS Components with Le Wagon',
   date_in_series_pattern: false,
   status: 'upcoming',
   time: 1584034200000,
   local_date: '2020-03-12',
   local_time: '18:30',
   updated: 1581593602000,
